/* Global Styles */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* Primary Colors */
  --primary-blue: #4F46E5;
  --primary-blue-light: #6366F1;
  --primary-blue-dark: #3730A3;
  
  /* Accent Colors */
  --accent-purple: #8B5CF6;
  --accent-cyan: #06B6D4;
  --accent-emerald: #10B981;
  --accent-amber: #F59E0B;
  --accent-rose: #EF4444;
  
  /* Background */
  --bg-primary: #0F0F23;
  --bg-secondary: #1A1A3E;
  --bg-tertiary: #252548;
  --bg-card: #2A2A54;
  --bg-hover: #323262;
  
  /* Text */
  --text-primary: #F8FAFC;
  --text-secondary: #CBD5E1;
  --text-muted: #94A3B8;
  
  /* Shadows */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.5);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.6);
  --shadow-glow: 0 0 20px rgba(79, 70, 229, 0.3);
  
  /* Gradients */
  --gradient-primary: linear-gradient(135deg, var(--primary-blue) 0%, var(--accent-purple) 100%);
  --gradient-bg: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 50%, var(--bg-tertiary) 100%);
  --gradient-card: linear-gradient(135deg, rgba(42, 42, 84, 0.9) 0%, rgba(37, 37, 72, 0.9) 100%);
}

body {
  background: var(--gradient-bg);
  color: var(--text-primary);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  min-height: 100vh;
  overflow-x: hidden;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 50%, rgba(79, 70, 229, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(6, 182, 212, 0.1) 0%, transparent 50%);
  pointer-events: none;
  z-index: -1;
}

.app {
  min-height: 100vh;
  position: relative;
}

/* Enhanced Auth Container */
.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  position: relative;
}

.auth-card {
  background: var(--gradient-card);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 24px;
  box-shadow: var(--shadow-xl), var(--shadow-glow);
  padding: 3rem 2.5rem;
  min-width: 420px;
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.auth-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--gradient-primary);
}

.auth-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-xl), 0 0 40px rgba(79, 70, 229, 0.4);
}

.auth-card h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  font-weight: 800;
  background: var(--gradient-primary);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: none;
}

.auth-card .subtitle {
  color: var(--text-secondary);
  margin-bottom: 2.5rem;
  font-size: 1.1rem;
  font-weight: 400;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.auth-input {
  padding: 1rem 1.25rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.auth-input:focus {
  outline: none;
  border-color: var(--primary-blue);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.2);
  transform: translateY(-2px);
}

.auth-input::placeholder {
  color: var(--text-muted);
}

.auth-button {
  background: var(--gradient-primary);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 1rem 2rem;
  font-weight: 600;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.auth-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg), 0 0 20px rgba(79, 70, 229, 0.4);
}

.auth-button:active {
  transform: translateY(0);
}

.auth-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.auth-link {
  color: var(--text-secondary);
  font-size: 1rem;
  margin-top: 1rem;
}

.auth-link button {
  background: none;
  border: none;
  color: var(--primary-blue-light);
  text-decoration: none;
  font-weight: 600;
  cursor: pointer;
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
  transition: all 0.2s ease;
}

.auth-link button:hover {
  background: rgba(79, 70, 229, 0.1);
  color: var(--primary-blue);
}

/* Enhanced Dashboard */
.dashboard-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
}

.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
  padding: 2rem 0;
}

.dashboard-title {
  font-size: 3rem;
  font-weight: 800;
  background: var(--gradient-primary);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 0;
  letter-spacing: -0.02em;
}

.dashboard-subtitle {
  color: var(--text-secondary);
  font-size: 1.2rem;
  margin-top: 0.5rem;
  font-weight: 400;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  background: var(--gradient-card);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 1rem 1.5rem;
  box-shadow: var(--shadow-md);
}

.user-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--gradient-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.1rem;
}

.user-details span {
  color: var(--text-secondary);
  font-size: 0.9rem;
  display: block;
}

.user-details strong {
  color: var(--text-primary);
  font-weight: 600;
}

.logout-button {
  background: linear-gradient(135deg, var(--accent-rose) 0%, #DC2626 100%);
  color: white;
  border: none;
  border-radius: 10px;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.95rem;
}

.logout-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

/* Enhanced Cards Grid */
.cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.sensor-card {
  background: var(--gradient-card);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 2rem;
  text-align: center;
  box-shadow: var(--shadow-lg);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.sensor-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--card-gradient, var(--gradient-primary));
  opacity: 0.8;
}

.sensor-card:nth-child(1) { --card-gradient: linear-gradient(135deg, var(--accent-cyan) 0%, var(--primary-blue) 100%); }
.sensor-card:nth-child(2) { --card-gradient: linear-gradient(135deg, var(--accent-emerald) 0%, var(--accent-cyan) 100%); }
.sensor-card:nth-child(3) { --card-gradient: linear-gradient(135deg, var(--accent-amber) 0%, var(--accent-emerald) 100%); }
.sensor-card:nth-child(4) { --card-gradient: linear-gradient(135deg, var(--accent-purple) 0%, var(--accent-rose) 100%); }

.sensor-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: var(--shadow-xl), 0 0 30px rgba(79, 70, 229, 0.2);
}

.sensor-card-icon {
  width: 60px;
  height: 60px;
  margin: 0 auto 1.5rem;
  border-radius: 16px;
  background: var(--card-gradient, var(--gradient-primary));
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  box-shadow: var(--shadow-md);
}

.sensor-card h3 {
  font-size: 1.3rem;
  margin-bottom: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  letter-spacing: -0.01em;
}

.sensor-value {
  font-size: 3rem;
  font-weight: 800;
  margin: 1rem 0;
  background: var(--card-gradient, var(--gradient-primary));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -0.02em;
  line-height: 1;
}

.sensor-unit {
  font-size: 1rem;
  color: var(--text-secondary);
  font-weight: 500;
  margin-top: 0.5rem;
}

.valve-controls {
  margin-top: 1.5rem;
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.valve-button {
  padding: 0.875rem 1.75rem;
  border: none;
  border-radius: 12px;
  background: var(--gradient-primary);
  color: white;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-md);
  position: relative;
  overflow: hidden;
}

.valve-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg), 0 0 20px rgba(79, 70, 229, 0.3);
}

.valve-button.close {
  background: linear-gradient(135deg, var(--accent-rose) 0%, #DC2626 100%);
}

/* Enhanced Chart Section */
.chart-section {
  background: var(--gradient-card);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 24px;
  padding: 2.5rem;
  margin-top: 2rem;
  box-shadow: var(--shadow-xl);
  position: relative;
  overflow: hidden;
}

.chart-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(135deg, var(--accent-cyan) 0%, var(--primary-blue) 100%);
}

.chart-header {
  display: flex;
  justify-content: between;
  align-items: center;
  margin-bottom: 2rem;
}

.chart-title {
  font-size: 1.8rem;
  font-weight: 700;
  background: linear-gradient(135deg, var(--accent-cyan) 0%, var(--primary-blue) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 0;
}

.chart-controls {
  display: flex;
  gap: 1rem;
}

.chart-control-btn {
  padding: 0.5rem 1rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-secondary);
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.chart-control-btn.active,
.chart-control-btn:hover {
  background: var(--primary-blue);
  color: white;
  border-color: var(--primary-blue);
}

/* Loading States */
.loading-spinner {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Status Indicators */
.status-indicator {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 50px;
  font-size: 0.875rem;
  font-weight: 500;
  margin-top: 1rem;
}

.status-indicator.online {
  background: rgba(16, 185, 129, 0.1);
  color: var(--accent-emerald);
  border: 1px solid rgba(16, 185, 129, 0.2);
}

.status-indicator.offline {
  background: rgba(239, 68, 68, 0.1);
  color: var(--accent-rose);
  border: 1px solid rgba(239, 68, 68, 0.2);
}

.status-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: currentColor;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

/* Responsive Design */
@media (max-width: 1200px) {
  .dashboard-container {
    padding: 1.5rem;
  }
  
  .cards-grid {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 1.5rem;
  }
}

@media (max-width: 768px) {
  .dashboard-header {
    flex-direction: column;
    text-align: center;
    gap: 1.5rem;
  }
  
  .dashboard-title {
    font-size: 2.5rem;
  }
  
  .cards-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .auth-card {
    min-width: 90vw;
    padding: 2rem 1.5rem;
  }
  
  .valve-controls {
    flex-direction: column;
  }
  
  .chart-section {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .dashboard-container {
    padding: 1rem;
  }
  
  .sensor-value {
    font-size: 2.5rem;
  }
  
  .auth-card {
    padding: 1.5rem 1rem;
  }
}
